.react-tabs {
  -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
  border-bottom: 2px solid #333; /* Dark bottom border */
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; /* Ensure tabs are in a horizontal row */
}

.react-tabs__tab {
  display: inline-block;
  border: 1px solid transparent;
  border-bottom: none;
  bottom: -1px;
  position: relative;
  list-style: none;
  padding: 10px 20px; /* Adjust for wider tabs */
  cursor: pointer;
  background-color: #444; /* Dark grey/black background */
  color: white; /* White text */
  margin-right: 2px; /* Space between tabs */
}

.react-tabs__tab:hover {
  background-color: #555; /* Slightly darker on hover */
}

.react-tabs__tab--selected {
  background: #ff8c00; /* Orange background for selected tab */
  color: black;
  border-color: #ff8c00; /* Orange border color */
  border-radius: 5px 5px 0 0;
}


.react-tabs__tab--disabled {
  color: GrayText;
  cursor: default;
}

.react-tabs__tab:focus {
  outline: none;
}

.react-tabs__tab:focus:after {
  content: '';
  position: absolute;
  height: 5px;
  left: -4px;
  right: -4px;
  bottom: -5px;
  background: #fff;
}

.react-tabs__tab-panel {
  display: none;
}

.react-tabs__tab-panel--selected {
  display: block;
}
